<template>
  <div v-theme:col="'narrow'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索" />
    <div v-for="(blog,index) in filterSearch" :key="index" class="single-blog">
      <router-link :to="'/blog/'+blog.id">
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
      </router-link>
      <p>{{blog.content | to-limit}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    this.$http
      .get("https://vue-demo-d90fc.firebaseio.com/posts.json")
      .then(function(data) {
        //console.log(data);
        return data.json();
      })
      .then(function(data) {
        // console.log(data);
        var blogsArray = [];
        for (let key in data) {
          //console.log(data[key]);
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;
        console.log(this.blogs);
      });
  },
  computed: {
    filterSearch() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search.toLowerCase());
      });
    }
  },
  filters: {
    toUppercase: function(value) {
      return value.toUpperCase();
    }
  },
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    }
  }
};
</script>

<style>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
}
.single-blog a {
  text-decoration: none;
}
input[type="text"] {
  display: block;
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}
</style>



