<template>
  <div class="add-blog">
    <h2></h2>
    <form v-if="!submited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>vue1</label>
        <input type="checkbox" value="vue1.js" v-model="blog.categories" />
        <label>vue2</label>
        <input type="checkbox" value="vue2.js" v-model="blog.categories" />
        <label>vue3</label>
        <input type="checkbox" value="vue3.js" v-model="blog.categories" />
        <label>vue4</label>
        <input type="checkbox" value="vue4.js" v-model="blog.categories" />
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="(item,index) in authors" :key="index">{{item}}</option>
      </select>
      <button @click.prevent="post">添加博客</button>
    </form>

    <div v-if="submited">
      <h3>发送成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="(item,index) in blog.categories" :key="index">{{item}}</li>
      </ul>

      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },
      authors: ["zhangsan", "lisi", "wangwu"],
      submited: false
    };
  },
  methods: {
    post: function() {
      //https://jsonplaceholder.typicode.com/posts

      this.$http
        .post("https://vue-demo-d90fc.firebaseio.com/posts.json", this.blog)
        .then(function(data) {
          console.log(data);
          this.submited = true;
        });
    }
  }
};
</script>


<style scoped>
.add-blog * {
  box-sizing: border-box;
}
.add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label {
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}
textarea {
  height: 200px;
}
#checkboxes label {
  display: inline-block;
  margin-top: 0;
}
#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}
button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>



